.newsletter-container{
  background-color: skyblue;
  padding: 80px 90px 70px 90px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.newsletter-container h2{
  padding-bottom: 10px;
}

.newsletter-container-text{
  font-size: 18px;
  width: 750px;
  text-align: center;
  line-height: 30px;
}

.newsletter-searchbox-container{
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.newsletter-searchbox-container input{
  padding: 8px;
  width: 350px;
  font-size: 17px;
}

.newsletter-searchbox-container div{
  background-color: orangered;
  padding: 5px 15px;
  display: flex;
  align-items: center;
  border: none;
  font-size: 17px;
  font-weight: bold;
}

.newsletter-searchbox-container div:hover{
  background-color: rgb(7, 7, 77);
  color: #fff;
}

@media screen and (max-width: 450px){

  .newsletter-container{
    padding: 40px 20px 40px 20px;
  }

  .newsletter-container-text{
    font-size: 18px;
    width: 100%;
    text-align: center;
    line-height: 30px;
  }

  .newsletter-searchbox-container{
    display: block;
  }

  .newsletter-searchbox-container div{
    padding: 5px 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    margin-top: 10px;
    width: max-content;
    text-align: center;
  }

}