.hotel-page-container{
    background-color: rgb(193, 231, 247);
    padding: 50px 90px;
    display: grid;
    grid-template-columns: 0.1fr 2fr;
}


.hotel-page-sidebar-top{
    background-color: white;
    margin-top: 50px;
    padding: 20px 20px 1px 20px;
}

.hotel-page-sidebar-top input{
    display: block;
    margin-top: 20px;
    padding: 10px;
    font-size: 16px;
    color: #000;
}

.hotel-page-sidebar-top input[type="date"]{
    width: 220px;
}

.hotel-page-sidebar-top .hotel-btn-search{
    background-color: orangered;
    width: 220px;
    margin: 40px 0px 20px 0px;
    padding: 10px;
    display: flex;
    justify-content:  center;
    align-items: center;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
}

.hotel-page-sidebar-top .hotel-btn-search:hover{
    background-color:rgb(7, 7, 77);
    color: #fff;
}

.hotel-page-sidebar-bottom{
    background-color: white;
    margin-top: 30px;
    padding: 20px 20px 1px 20px;
}

.rating{
    padding-top: 10px;
}

.rating input{
    margin-right: 10px;
}

.rating:first-of-type{
    padding-top: 10px;
}

.rating:last-of-type{
    padding-bottom: 30px;
}

.rating input{
    padding-right: 20px;
}
