.footer-container{
  background-color: #000;
  color: #fff;
  padding: 90px 90px 40px 90px;
}

.footer-container-grid{
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: 10px;
}

.footer-container p{
  padding-bottom: 15px;
}

.gutter-box {
  padding: 8px 0;
  background: #00a0e9;
}

.footer-socials{
  font-size: 30px;
  padding-top: 60px;
}

.socials{
  margin-right: 20px;
}

.footer-address{
  display: flex;
  align-items: center;
  text-align: left;
}

.footer-address .footer-address-icon{
 padding-right: 15px;
 font-size: 18px;
}

.footer-copyright {
  padding-top: 40px;
  text-align: center;
}

@media screen and (max-width: 450px){

  .footer-container{
    padding: 40px 20px 40px 20px;
  }

  .footer-container-grid{
    width: 100%;
    grid-template-columns: 1fr;
    gap: 20px;
  }

}

@media screen and (max-width: 820px){

  .footer-container{
    padding: 40px 20px 40px 20px;
  }

  .footer-container-grid{
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
  }

}