.restuarants{
  padding: 80px 90px 15px 90px;
}

.restuarants-card-container{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  text-align: left;
  padding-top: 20px;
  padding-bottom: 50px;
}

.restuarants-cards {
  background-color: #fff;
  /* margin-right: 30px; */
}

.rating{
  padding-left: 22px;
}

.ratings{
  margin-top: -15px;
}

.restuarants-cards h2{
  font-size: 18px;
  padding: 15px 20px 0px 20px;
}

.restuarants-cards h4{
  padding: 5px 20px 0px 20px;
  text-align: right;
}

.restuarants-cards p{
  font-size: 17px;
  line-height: 27px;
  padding: 5px 20px;
}


.restuarants-cards img{
  width: 100%;
}

