@media screen and (max-width: 450px){

  .restuarants{
    padding: 40px 20px 40px 20px;
  }

  .restuarants-card-container{
    grid-template-columns: 1fr;
    gap: 40px;
  }

}

@media screen and (max-width: 820px){

  .restuarants{
    padding: 40px 20px 40px 20px;
  }

  .restuarants-card-container{
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }

}