.hotel-card-container{
  margin: 40px 40px 40px 30px;
  overflow-x: hidden;
}

.hotel-cards-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}

.hotel-cards{
  width: 250px;
  background-color: #fff;
  margin-top: 30px;
  padding: 0px 0px 30px 0px;
}

.hotel-cards h2{
  font-size: 20px;
  padding: 15px 20px;
}

.countries-price{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
  font-size: 20px;
}

.hotel-cards p{
  font-size: 15px;
  line-height: 25px;
  padding: 0px 20px;
}

.hotel-cards img{
  width: 250px;
}

.hotel-card-rating{
  margin-bottom: 9px;
}