.hotels{
    padding: 80px 90px 25px 90px;
  }
  
  .hotels-card-container{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    text-align: left;
    padding-top: 20px;
    padding-bottom: 50px;
  }
  
  .hotels-cards {
    /* width: 290px; */
    background-color: #fff;
    /* margin-right: 30px; */
    padding-bottom: 20px;
  }
  
  .rating{
    margin-top: 10px;
    padding-left: 22px;
  }
  
  .ratings{
    margin-top: -15px;
    padding-bottom: 20px;
  }
  
  .hotels-cards h2{
    font-size: 20px;
    padding: 15px 20px 0px 20px;
  }
  
  .countries-price{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    font-size: 20px;
  }
  
  .hotels-cards p{
    font-size: 17px;
    line-height: 27px;
    padding: 5px 20px;
  }
  
  
  .hotels-cards img{
    width: 100%;
  }

