  @media screen and (max-width: 450px){

    .hotels{
      padding: 40px 20px 40px 20px;
    }
  
    .hotels-card-container{
      grid-template-columns: 1fr;
      gap: 40px;
    }
  
  }

  @media screen and (max-width: 820px){

    .hotels{
      padding: 40px 20px 40px 20px;
    }
  
    .hotels-card-container{
      grid-template-columns: 1fr 1fr;
      gap: 40px;
    }
  
  }