.searchBox{
  margin: 0px 0px;
}

.search{
  width: 320px;
  height: 50px;
  font-size: 18px;
}

.search .input{
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border-right: 0px;
}

.search .select{
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.search:focus{
  outline: none;
}

.search_btn{
  width: 100px !important;
  background-color: rgb(7, 7, 77);
  font-size: 16px;
  /* padding-top: 3px; */
  font-weight: bold;
  color: #fff;
}

.search_btn:hover{
  background-color: orangered;
}