.destination{
  background-color: #fff;
  /* margin: 70px 0px 0px 0px; */
  padding: 50px 90px 10px 90px;
}
.destination-cards{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  text-align: left;
  padding-top: 20px;
  padding-bottom: 50px;
}

.cards{
  /* width: 287px; */
  max-width: 350px;
  background-color: rgb(113, 176, 200);
  padding-bottom: 11px;

}

.cards h2{
  font-size: 20px;
  padding: 9px 15px;
}

.cards p{
  font-size: 18px;
  padding: 0px 15px;
}

.cards img{
  width: 100%;
  height: 290px;
}

