@media screen and (max-width: 450px){

  .destination{
    padding: 40px 20px 40px 20px;
  }

  .destination-cards{
    grid-template-columns: 1fr;
    gap: 40px;
  }

}

@media screen and (max-width: 820px){

  .destination{
    padding: 40px 20px 40px 20px;
  }

  .destination-cards{
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }

}