.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  left: 0px;
  width:100%;
  padding: 0px;
  margin: 0px;
  top: 0px;
  background-color: transparent;
  transition: 0.5s;
  z-index: 2;
  color: #fff;
}

.header-extension{
  position: fixed;
  top:0px;
  left:0px;
  background-color: skyblue;
  z-index: 5;
  width: 100%;
  height: 100px;
  padding: 0px 90px;
  box-sizing: border-box;
}

.logo{
  font-weight: bold;
  font-size: 22px;
}

.navbar{
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  display:flex;
  justify-content: space-around;
  padding-left: -30px;
}

.navbar a{
  padding-left: 20px;
  text-decoration: none;
  font-size: 18px;
  color: #fff;
}

.navbar a:hover{
  color: orangered;
}


@media screen and (max-width: 900px) {
  .header-extension{
    padding: 0px 50px;
  }
}

@media screen and (max-width: 800px) {
  .navbar{
    display: none;
  }
}

@media screen and (max-width: 450px){

  .header-extension{
    padding: 0px 20px;
  }

  .navbar{
    display: none;
  }
}