.contact-container{
  padding: 90px;
}

.contact-container h2{
  font-size: 29px;
}


.contact-information{
  display: grid;
  grid-template-columns: 2fr 1.5fr 2.5fr 2.5fr 2.5fr;
  gap: 10px;
  padding: 40px 0px;
}

.contact-information p{
  font-size: 16px;
}

.contact-information div{
  margin-bottom: 20px;
}

.contact-form{
  display: grid;
  grid-template-columns: 2fr 2fr;
  padding-bottom: 50px;
  padding-top: 30px;
  gap:60px;
}

.contact-form input{
  display: block;
  padding: 10px;
  font-size: 20px;
  margin-bottom: 10px;
  width: 100%;
  font-family: 'Times New Roman', Times, serif;
}

.contact-form textarea{
  font-size: 20px;
  padding: 5px 600px 200px 10px;
  font-family: 'Times New Roman', Times, serif;
}

/* .contact-form input:nth-last-of-type(1){
  height: 190px;
} */


@media screen and (max-width: 1147px) {
  .contact-information{
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 1000px) {
  .contact-information{
    grid-template-columns: repeat(3,1fr);
  }
}

@media screen and (max-width: 1000px) {
  .contact-information{
    grid-template-columns: repeat(2,1fr);
    gap:30px;
  }
}

@media screen and (max-width: 900px) {
  .contact-container{
   padding: 50px;
  }

  .contact-form{
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 700px) {
  .contact-information{
    grid-template-columns: repeat(1,1fr);
  }

  .contact-information div{
    text-align: center;
  }

  .contact-container h2{
    text-align:center;
  }
}