.about-us{
  display: flex;
  width: 100%;
  padding: 90px;
  

}

.img{
  width: 50%;
}
.img-text{
  width: 50%;
  display: flex;
  
}
.mission{
  display: inline;
  
}

#about-us-container{
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 30px;
  padding: 100px;
}

/* #about-us-info{
padding-left: 50px;
} */

.our-pic{
  width: 600px;
}

#about-us-image{
  background-color: #f0f0f0;
}

#about-us-image{
  width: 700px;
  text-align: center;
}

#about-us-image img{
  height: 450px;
  width: 450px !important;
}

#about-us-inner img{
  height: 450px;
  width: 450px !important; 
}

#about-us-info-head{
  display: flex;
  padding-top: 30px;
}

.about-us-child{
  margin-right: 20px;
  border-radius: 25px;
  padding: 15px;
  opacity: .6;
  font-weight: 600;
  cursor: pointer;
}

.about-us-child:hover{
  opacity: 1;
}

.active{
  background-color: #17a2b8;
  color:#ffffff;
  opacity: 1;
}

#about-us-info-child{
  margin-top: 50px;
}

#info-title{
  margin-bottom: 30px;
  font-size: 25px;
  font-weight: 400;
}

#info-content{
  opacity: .8;
  text-align: justify;
  line-height: 27px;
}

/* frequently asked section */

#about-us-faqs{
  padding: 60px 90px;
  background-color: #eef6f8;
}

#about-us-faqs h5{
  opacity: 0.5;
  font-weight: normal;
  font-size: 15px;
}

#about-us-faqs span{ 
  font-weight: bold;
}

#about-us-faqs h2{ 
  font-weight: normal;
  font-size:35px;
  padding-bottom: 50px;
}

#about-us-questions{
  padding: 100px;
}