.WhyChooseUs-container{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap:50px;
  background-color: #fff;
  padding: 90px 90px 50px 90px;
}

.WhyChooseUs-container h3{
  padding-bottom: 25px;
}

.WhyChooseUs-container p{
  font-size: 17px;
  line-height: 30px;
}

.WhyChooseUs-container-left{
  /* width: 50%; */
  /* padding-top: 50px; */
}

.WhyChooseUs-container-left span{
  color: rgb(7, 7, 77);
}

.read-more-btn{
  border: 1px solid rgb(7, 7, 77);
  border-radius: 50px;
  padding: 5px;
  width: 110px;
  cursor: pointer;
  font-size: 17px;
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.read-more-btn:hover{
  background-color: rgb(7, 7, 77);
  color: white;
}

.WhyChooseUs-container-right{
  /* width: 50%; */
  /* padding: 80px 10px 50px 50px; */
}

.WhyChooseUs-container-right span{
  color: rgb(7, 7, 77);
  font-weight: bold;
}

.WhyChooseUs-container-right-review{
  background-color: skyblue;
  padding: 20px 10px 20px 20px;
  display: flex;
  /* width: 60px; */
}

.WhyChooseUs-container-right-review img{
  width: 40px;
  margin-right: 30px;
  margin-left: 10px;
}

.WhyChooseUs-container-right-review div{
  width: 400px;
  height: 230px;
}

.whyChoose-us-text{
  width: 400px;
}

.WhyChooseUs-container-right-review Carousel{
  padding-bottom: 30px;
}

