@media screen and (max-width: 450px){

  .WhyChooseUs-container{
    padding: 40px 20px 40px 20px;
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .WhyChooseUs-container-left{
    width: 100%; 
    padding-right: 20px;
    text-align: justify;
  }

  .WhyChooseUs-container-right{
    width: 75%; 
    padding-right: 20px;
    display: none;
  }

  .WhyChooseUs-container-right-review img{
    display: none;
  }

  .whyChoose-us-text{
    width: 310px;
  }
}

@media screen and (max-width: 820px){

  .WhyChooseUs-container{
    padding: 40px 20px 40px 20px;
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .WhyChooseUs-container-left{
    width: 100%; 
    padding-right: 20px;
    text-align: justify;
  }

  .WhyChooseUs-container-right{
    width: 100%; 
    padding-right: 0px;
    /* display: none; */
  }

  .WhyChooseUs-container-right-review img{
    display: none;
  }

  .whyChoose-us-text{
    width: 310px;
  }
}