.cardBox{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
  text-align: center;
  z-index: 2;
  padding: 0px 90px 40px 90px;
  width: 100%;
  box-sizing:border-box;
}

.card{
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  margin-top: -20px;
}

.card_img{
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
  padding-top: 20px;
}

.card h2{
  font-size: 23px;
  line-height: 23px;
  padding-bottom: 20px;
  color: rgb(7, 7, 77);
}

.card p{
  font-size: 18px;
  line-height: 27px;
}


