@media screen and (max-width: 450px){
  .cardBox{
    grid-template-columns: 0.9fr;
    gap: 40px;
    padding: 40px 20px 40px 20px;
  }

  .card{
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    margin-top: -20px;
  }
}

@media screen and (max-width: 820px){

  .cardBox{
    padding: 40px 20px 40px 20px;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }

  .card{
    margin-top: 0px;
  }

}