.tour-card-container{
  margin: 40px 40px 40px 30px;
  overflow-x: hidden;
}

.tour-cards-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}

.tour-cards{
  width: 250px;
  background-color: #fff;
  margin-top: 30px;
  padding: 0px 0px 30px 0px;
}

.tour-cards h2{
  font-size: 20px;
  padding: 15px 20px;
}

.tour-cards p{
  font-size: 15px;
  line-height: 25px;
  padding: 0px 20px;
}

.tour-cards img{
  width: 250px;
  height: 300px;
}

.tour-card-rating{
  margin-bottom: 9px;
}