.funFacts-container{
  text-align: center;
  background-color: skyblue;
  padding: 80px 0px;
  padding-top: 90px;
  padding-bottom: 60px;
  color: black;
}

.funFacts-container-text{
  font-size: 22px;
  padding-top: 9px;
}

.funFacts-details{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding: 90px 150px 50px 150px;
}

.details{
  font-size: 25px;
}

.description{
  font-size: 18px;
  padding-top: 10px;
}

@media screen and (max-width: 450px){

  .funFacts-container{
    padding: 40px 20px 40px 20px;
  }

  .funFacts-details{
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 15px;
    padding: 40px 0px 50px 10px;
  }

  .details{
    font-size: 18px;
  }

  .description{
    font-size: 16px;
    padding-top: 0px;
  }

}

@media screen and (max-width: 820px){

  .funFacts-container{
    padding: 40px 20px 40px 20px;
  }

  .funFacts-details{
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 15px;
    padding: 40px 0px 50px 10px;
  }

  .details{
    font-size: 18px;
  }

  .description{
    font-size: 16px;
    padding-top: 0px;
  }

}

