#topsection {
  width: 100%;
  height: 100vh;
}

#topsection-body {
  width: 100%;
  height: 70vh;
  background-size: 100%;
  background-position: top center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position:relative;
}

#navbar-container{
  position:'absolute';
  top:'0px';
  width: 100%;
  padding: 0px 90px;

}

#topsection-small {
  font-size: 16px;
  margin-bottom: 30px;
  color: white;
}

#topsection-bold {
  font-weight: 500;
  margin-right: 10px;
  cursor: pointer;
}

#fade {
  opacity: 0.7;
}

#topsection-big {
  font-size: 60px;
  color: white;
  font-weight: 500;
}

