.hero{
  background-color: rgb(113, 176, 200);
  color: white;
  width: 100%;
}

.hero-section{
  background-image: url(../assets/images/hero_image.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: cover;
  width: 100%;
  height: 100vh;
  padding: 0px 90px;
  box-sizing: border-box;
}

.hero-text{
  padding: 190px 0px 90px 0px;
}

.hero-text h2{
  letter-spacing: 2px;
  color: rgb(7, 7, 77);
}

.hero-text h2 span{
  font-size: 60px;
  color: white;
  
}

.hero-text p{
  font-size: 19px;
  padding-top: 20px;
  font-weight: bold;
}

@media screen and (max-width: 450px){
  .hero-text{
    padding: 10px 0px 40px 0px;
  }

  .hero-section{
    width: 100%;
    height: 250%;
    padding: 0px 20px;
  }
}

@media screen and (max-width: 820px){

  .hero{
    padding: 0px 0px 0px 0px;
  }

  .hero-section{
    width: 100%;
    height: 220%;
    padding: 0px 20px;
  }

  .hero-text{
    padding: 90px 0px 90px 0px;
  }
}


@media screen and (max-width: 912px){

  .hero{
    padding: 0px 0px 0px 0px;
  }

  .hero-section{
    width: 100%;
    height: 220%;
    padding: 0px 20px;
  }

  .hero-text{
    padding: 90px 0px 90px 0px;
  }
  

}